import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import './Card.css'
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import DeleteIcon from '@material-ui/icons/Delete';

const useStyles = makeStyles({
  root: {
    maxWidth: 345,
    marginLeft: 10,
    marginBottom: 10
  },
  profileimage: {
    width: 200,
    height: 200,
  },
});

export default function ImgMediaCard(props) {
  const {
    photo,
    name,
    phone,
    _id,
    price,
    leftSideOfvehicle,
    rightSideOfvehicle,
    vehicleLicense,
    drivingLicense,
    backOfvehicle,
    frontOfvehicle,
  } = props.data;
  const classes = useStyles();

  const changeState = () => {
    props.statusChangeHandler(props.data._id);
  };

  const changeRemove = () => {
    props.statusRemoveHandler(props.data._id);
  };

  const viewFiles = () => {
    props.viewFilesHandler(

      leftSideOfvehicle,
      rightSideOfvehicle,
      vehicleLicense,
      drivingLicense,
      backOfvehicle,
      frontOfvehicle
    );
  };
  return (
    <Card className={classes.root}>
      <CardActionArea>
        <CardMedia
          className={classes.profileimage}
          component="img"
          alt="Contemplative Reptile"
          height="140"
          image={`https://jetek-tr5el.ondigitalocean.app${photo}`}
          title="Contemplative Reptile"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h4">
            {name}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {price}SAR
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" startIcon={<DeleteIcon />}
          variant="contained" color="secondary" onClick={changeRemove}>
          حذف المنتج
        </Button>
        <Button size="small" variant="contained" style={{ margin: 2 }} color="primary" onClick={changeState}>
          تعديل المنتج
        </Button>

      </CardActions>
    </Card>
  );
}
